import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Footer extends Component {
    constructor(props) {
        super(props);

        this.showCookies = () => {
            // Show a pop-up or sometihng.. alert() is how it was on the original site though
            alert("Animasher uses cookies to track who is who. If you don't want a cookie, you can disable them in your browser, but then you will not be able to log in to the site.");
        };
    }

    render() {
        return (
            <footer>
                <ul>
                    <li><Link to="/">Animations</Link></li>{" - "}
                    <li><Link to="/feedback">Feedback</Link></li>{" - "}
                    <li><Link to="/tos">Terms of Service</Link></li>{" - "}
                    <li><Link to="/license">License</Link></li>{" - "}
                    <li><Link to="" onClick={this.showCookies}>Cookies</Link></li>{" - "}
                    <li><Link to="/about">About</Link></li>{" - "}
                    <li><Link to="/sitemap.xml">XML</Link></li>{" - "}
                    <li><Link to="/rules">Rules</Link></li>{" - "}
                    <li><Link to="/parents">For parents</Link></li>{" - "}
                    <li><Link to="/contact">Contact</Link></li>
                </ul>
            </footer>
        );
    }
}
