import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

export default class Card extends Component {
    static propTypes = {
        title: PropTypes.string.isRequired,
        content: PropTypes.element.isRequired,
        footer: PropTypes.object,
        className: PropTypes.string
    };

    static defaultProps = {
        footer: {},
        className: ""
    }

    render() {
        if (this.props.footer.text && this.props.footer.link) this.footer = (
            <h3 className="card--footer"><Link to={this.props.footer.link}>{this.props.footer.text}</Link></h3>
        )

        return (
            <div className={"card " + this.props.className}>
                <p className="card--title">{this.props.title}</p>
                <div className={"card--content box"}>{this.props.content || this.props.children}</div>
                {this.footer}
            </div>
        );
    }
}
