import React, { Component, createRef } from "react";
import { Link } from "react-router-dom";

import Logo from "../../assets/img/logo-blur.png";

export default class Header extends Component {
    constructor(props) {
        super(props);

        this.state = {
            searchDisabled: true,
            loginDisabled: true
        };

        this.searchField = createRef();
        this.usernameField = createRef();
        this.passwordField = createRef();

        this.searchInput = e => {
            e.target.value ? this.setState({ searchDisabled: false }) : this.setState({ searchDisabled: true });
        };

        this.loginInput = () => {
            if (this.usernameField.current.value.length > 0 && this.passwordField.current.value.length > 0) this.setState({ ...this.state, loginDisabled: false });
            else this.setState({ ...this.state, loginDisabled: true });
        };

        this.search = e => {
            e.preventDefault();
            this.searchField.current.value = "Search Successful";
        };
    }

    render() {
        return (
            <header>
                <Link to="/">
                    <img src={Logo} alt="Logo" className="logo" />
                </Link>
                <ul className="menu">
                    <li><Link to="/watch">Watch</Link></li>
                    <li><Link to="/create">Create</Link></li>
                    <li><Link to="/leaders">Leaders</Link></li>
                    <li><Link to="/learn">Learn</Link></li>
                </ul>
                <ul className="lang">
                    <li><Link to="/sv">SV</Link></li>
                    <li><Link to="/de">DE</Link></li>
                    <li><Link to="/it">ITA</Link></li>
                    <li><Link to="/es">ESP</Link></li>
                    <li><Link to="/ja">日本語</Link></li>
                    <li><Link to="/pt">POR</Link></li>
                    <li><Link to="/en">ENG</Link></li>
                </ul>
                <form
                    action=""
                    id="search"
                    method="GET"
                    onSubmit={this.search}
                >
                    <input
                        type="text"
                        name="search"
                        id="searchTerm"
                        placeholder="Search animations.."
                        onInput={this.searchInput}
                        ref={this.searchField}
                    />
                    <input
                        type="submit"
                        id="searchSubmit"
                        value="search"
                        disabled={this.state.searchDisabled}
                    />
                </form>

                <div className="login">
                    <Link to="/signup">signup</Link><span>/</span>
                    <form
                        action=""
                        id="login"
                        method="GET"
                        onSubmit={this.login}
                    >
                        <input
                            type="text"
                            name="username"
                            id="username"
                            placeholder="login"
                            onInput={this.loginInput}
                            ref={this.usernameField}
                        />
                        <input
                            type="password"
                            name="password"
                            id="password"
                            placeholder="••••••••"
                            onInput={this.loginInput}
                            ref={this.passwordField}
                        />
                        <input
                            type="submit"
                            value="ok"
                            disabled={this.state.loginDisabled}
                        />
                    </form>
                </div>
            </header>
        );
    }
}
