import React from "react";
import { useParams } from "react-router-dom";
import Card from "../Card";

export default function Watch() {
    const { id } = useParams();

    return (
        <div>
            <Card title="You are watching">
                <div className="title">Title of {id}</div>
                <div className="Video">Video</div>

                <div className="tabs">
                    <div className="active">Information</div>
                    <div>Share</div>
                    <div>Mark</div>
                    <div>Remix</div>
                    <div>Credits</div>
                </div>
                <div className="content">
                    Description and more info here...
                </div>
            </Card>
            <Card title="Author">
                Unknown user
            </Card>
            <Card title="Watch next">
                ...List of videos
            </Card>
        </div>
    );
}
