import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

function RecentActivity(props) {
    const data = props.data;
    const error = (<p style={{ color: "red" }}>Error!</p>);
    if (!data) {
        return error;
    }

    let timeAgo = (
        <p> {data.timeAgo + " ago:" } </p>
    );

    var message;
    try {
        switch (data.type) {
            case "rating":
                message = (
                    <p><Link to={data.post.link}>{data.post.name}</Link> was rated {data.rating}</p>
                );
                break;

            case "profileUpdate":
                message = (
                    <p><Link to={data.user.link}>{data.user.name}</Link> updated his/her profile</p>
                );
                break;

            case "comment":
                message = (
                    <>
                        <p><Link to={data.user.link}>{data.user.name}</Link> posted a comment on <Link to={data.comment.post.link}>{data.comment.post.name}</Link>:</p>
                        <p className="comment">{data.comment.content}</p>
                    </>
                );
                break;

            case "create":
                message = (
                    <p><Link to={data.user.link}>{data.user.name}</Link> created <Link to={data.post.link}>{data.post.name}</Link></p>
                );
                break;

            default:
                timeAgo = "";
                message = error;
        }
    } catch(e) {
        timeAgo = "";
        message = error;
    }

    return (
        <div className="activity">
            <i></i>
            {timeAgo}
            {message}
        </div>
    );
}

RecentActivity.propTypes = {
    data: PropTypes.object.isRequired
};

export default RecentActivity;
