import React from "react";
import { useParams } from "react-router-dom";
import Card from "../Card";

export default function Profile() {
    const { name } = useParams();

    return (
        <div className="container">
            <div className="grid">

                <Card
                    title={name + " Has Created"}
                    className="animations"
                    content={
                        <>
                            No animations created yet
                        </>
                    }
                />
                <div className="profile">
                    <div className="title">
                        Profile
                    </div>
                    <div className="box">
                        <b>Hi, I&apos;m {name} </b>
                    </div>
                </div>

                <Card
                    title="Messages"
                    className="animations"
                    content={
                        <>
                            <b>Send a message to {name}</b>
                            <textarea></textarea>
                            <button>Post message</button>
                        </>
                    }
                />
                <div className="follow active">
                    STOP FOLLOWING THIS MEMBER
                </div>
            </div>
        </div>
    );
}
