import React, { Component, createRef } from "react";

export default class Feedback extends Component {
    constructor(props) {
        super(props);

        this.element = createRef();
        this.form = createRef();
        this.email = createRef();
        this.error = createRef();
        var regex = /^(([^<>()\\.,;:\s@"]+(\.[^<>()\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/i;

        this.submit = e => {
            e.preventDefault();
            if (!this.email.current.value.match(regex)) return;
            try {
                // submit form here

                window.sessionStorage.setItem("success", true);
                window.location.reload();
            } catch(e) {
                // error handling goes here. Probably shouldn't just log it...

                const error = document.createElement("h2");
                error.style.color = "red";
                error.innerHTML = "An unknown error occurred... Please try again later or contact the developers on <code>inbox@animasher.com</code>";
                document.getElementById("feedbackpage").appendChild(error);
            }
        };

        this.success = () => {
            const text = document.createElement("h2");
            text.style.color = "lime";
            text.innerText = "Feedback form successfully submitted!";
            document.getElementById("feedbackpage").appendChild(text);
        };

        window.onload = () => {
            if (window.sessionStorage.getItem("success")) {
                window.sessionStorage.removeItem("success");
                this.success();
            }
        };
    }

    render() {
        return (
            <div id="feedbackpage">
                <h1 className="pink">Feedback</h1>
                <p>Tell us your thoughts</p>

                <form className="feedback" onSubmit={this.submit}>
                    <h3>Reasons for writing</h3>
                    <div className="reasons">
                        <input type="checkbox" name="bugReport" id="bugReport" value="bugReport"/>
                        <label htmlFor="bugReport">Wohoo! I&apos;ve found a bug!</label>
                        <br/>
                        <input type="checkbox" name="featureRequest" id="featureRequest" value="featureRequest"/>
                        <label htmlFor="featureRequest">Missing feature! Me want!</label>
                        <br/>
                        <input type="checkbox" name="lame" id="lame" value="lame"/>
                        <label htmlFor="lame">This site is lame!</label>
                        <br/>
                        <input type="checkbox" name="rule" id="rule" value="rule"/>
                        <label htmlFor="rule">This site is awesome!</label><br/>
                        <input type="checkbox" name="privacy" id="privacy" value="privacy"/><label htmlFor="privacy">Privacy complaint</label>
                        <br/>
                        <input type="checkbox" name="copyright" id="copyright" value="copyright"/>
                        <label htmlFor="copyright">Copyright infringement</label>
                        <br/>
                        <input type="checkbox" name="other" id="other" value="other"/>
                        <label htmlFor="other">Other</label><br/>
                    </div>

                    <h3>A little more detail please</h3>
                    <textarea
                        name="description"
                        id="description"
                        cols="30"
                        rows="10"
                        minLength="20"
                        ref={this.description}
                        onInput={this.input}
                    ></textarea>

                    <h3>Your email</h3>
                    <input
                        type="email"
                        name="email"
                        placeholder="Enter your email"
                        ref={this.email}
                    />

                    <i></i> {/* To fill out empty field */}
                    <input type="submit" value="Send feedback"/>
                </form>
            </div>
        );
    }
}
